import React, { Component } from 'react';
import { render } from 'react-dom';

const boxStyle = { height: 20, backgroundColor: 'black' };
const getInitialWidths = () =>
  Array(100)
    .fill(0)
    .map(() => Math.round(Math.random() * 50));

class App extends Component {
  state = {
    widths: getInitialWidths(),
  };

  doubleSize = () => {
    const widths = this.state.widths.map(n => n * 2);
    this.setState({ widths });
  };

  render() {
    return (
      <div>
        <button onClick={this.doubleSize}>Double Sizes</button>
        {this.state.widths.map((width, index) => (
          <div style={{ width, ...boxStyle }} key={index} />
        ))}
      </div>
    );
  }
}

render(<App />, document.getElementById('root'));



